<template>
    <div class="container">
        <md-card >
            <md-card-header>
                <md-field>
                    <label>Name</label>
                    <md-input v-model="name" disabled></md-input>
                </md-field>
                <md-field>
                    <label>Device ID</label>
                    <md-input v-model="deviceID" disabled></md-input>
                </md-field>
            </md-card-header>
        </md-card>
        <div class="group">
            <router-link to="/login/action">
                <button type="button" class="btn line-btn" @click="action('DEPOSIT')">Deposit</button>
            </router-link>
            <router-link to="/login/action">
                <button type="button" class="btn line-btn" @click="action('TRANSFER')">Transfer</button>
            </router-link>
            <router-link to="/login/action">
                <button type="button" class="btn line-btn" @click="action('LOAN')">Loan</button>
            </router-link>

            <router-link to="/" class="logout">
                <button type="button" class="btn btn-secondary logout">Logout</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'mainScreen',
    data(){
        return{
            name: '',
            deviceID: ''
        }
    },
    created(){
        this.$ga.page({
            page: '/login/main',
            title: 'Main',
            location: window.location.href
        })
    },
    mounted(){
        const vm = this;
        vm.$cookieStore.getCookie('NAME') ? vm.name = vm.$cookieStore.getCookie('NAME') : vm.name = '無填入姓名';
        vm.deviceID = vm.$cookieStore.getCookie('DEVICE_ID');
    },
    methods:{
        action(value){
            const vm = this;
            var tempNum = Number(vm.$cookieStore.getCookie(value));
            vm.$cookieStore.setCookie(value, tempNum+1);   
        }
    }
}
</script>

<style lang="scss" scoped>
.form{
  margin-top: 50px;
}
.group{
    margin-top: 70px;
}
.btn{
  display: block;
  width: 100%;
  margin-bottom: 20px;
}

.logout{
    margin-top: 100px;
}
</style>


