<template>
    <div class="container">
        <md-card>
            <md-card-header>
                <md-field>
                    <label>Name</label>
                    <md-input v-model="name" disabled></md-input>
                </md-field>
                <md-field>
                    <label>Device ID</label>
                    <md-input v-model="deviceID" disabled></md-input>
                </md-field>
            </md-card-header>
        </md-card>
        <h1>FINISH</h1>

        <div class="group">
            <router-link to="/login/main">
                <button type="button" class="btn btn-outline-light">Back To Main Page</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'finish',
    data(){
        return{
            name: '',
            deviceID: ''
        }
    },
    created(){
        this.$ga.page({
            page: '/login/finish',
            title: 'Finish',
            location: window.location.href
        })
    },
    mounted(){
        const vm = this;
        vm.$cookieStore.getCookie('NAME') ? vm.name = vm.$cookieStore.getCookie('NAME') : vm.name = '無填入姓名';
        vm.deviceID = vm.$cookieStore.getCookie('DEVICE_ID');
    },
}
</script>

<style lang="scss" scoped>
.container{
    background: rgb(37,200,117);
    color: #fff;
    text-align: center;
    h1{
        height: 50px;
        margin-top: 100px;
    }
}
.md-card{
    background: #fff;
    color: #000;
    text-align: left;
}
.group{
    margin-top: 50px;
}
</style>


